function CeltoFahr (c) {
    var cTOf= c * 9/5 +32
    
    return (cTOf)
}



function FahrtoCel (f) {
    var fTOc= (f-32) * 5/9 
    
    return (fTOc)

}



function CeltoKel (c) {
    var CeltoKel= c + 273.15 
    
    return (CeltoKel)
}



function KeltoCel(k) {
    var kTOc= k - 273.15 
    
    return (kTOc)
}



function KeltoFahr (k) {
    var kTOf= k * 9/5 - 459.67
    
    return (kTOf)
}



function FahrtoKel(f) {
    var fTOk= (f + 459.67) * 5/9
    
    return (fTOk)
}



module.exports ={
    FahrtoCel : FahrtoCel,
    CeltoFahr : CeltoFahr,
    CeltoKel : CeltoKel,
    KeltoCel : KeltoCel,
    KeltoFahr : KeltoFahr,
    FahrtoKel : FahrtoKel
}